<script lang="ts">
  import {toHex} from "src/util/nostr"
  import Input from "src/partials/Input.svelte"
  import Anchor from "src/partials/Anchor.svelte"
  import Content from "src/partials/Content.svelte"
  import Heading from "src/partials/Heading.svelte"
  import keys from "src/agent/keys"
  import {toast} from "src/app/ui"
  import {login} from "src/app"

  let npub = ""

  const logIn = () => {
    const pubkey = npub.startsWith("npub") ? toHex(npub) : npub

    try {
      keys.validate(pubkey)
    } catch (e) {
      toast.show("error", "Sorry, but that's an invalid public key.")

      return
    }

    login("pubkey", pubkey)
  }
</script>

<Content size="lg" class="text-center">
  <Heading>Login with a nostrkid Public Key</Heading>
  <section>
  <p>
    For read-only access, enter a public key below. The key should start
    with "npub".
  </p>
  </section>
  <section>
  <p> 
  Example: nostrkid 1 sprite205 : npub1falry06qh3ktl6evtf8enl2we9qarflncc299ujpda4kkqkhl7gs3995cd
  </p>
  </section>
  <div class="flex gap-2">
    <div class="flex-grow">
      <Input bind:value={npub} placeholder="npub...">
        <i slot="before" class="fa fa-key" />
      </Input>
    </div>
    <Anchor type="button" on:click={logIn}>Log In</Anchor>
  </div>
</Content>
